import {Editor} from "@tiptap/core";
import {StarterKit} from "@tiptap/starter-kit";
import Collaboration from "@tiptap/extension-collaboration";
import CollaborationCursor from "@tiptap/extension-collaboration-cursor";
import * as Y from "yjs";
import {HocuspocusProvider} from "@hocuspocus/provider";
import axios from "axios";

const API_URI = "http://api.localhost:5555";

const TOKEN = `eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VyX2lkIjoxNTgwOCwidHlwZSI6ImF1dGgiLCJleHAiOjE2ODAyNjQ5NzJ9.Rx1ly7fYjnzCPGPo2Q-18Z6Tf33VA9XYP2pbytjlRQ0`;

const ydoc = new Y.Doc();

const provider = new HocuspocusProvider({
  url: "ws://127.0.0.1:3003",
  name: "1",
  document: ydoc,
  token: TOKEN,
});

axios
  .post(`${API_URI}/user/me`, null, {
    headers: {
      Authorization: `Bearer ${TOKEN}`,
    },
  })
  .then((response) => {
    provider.setAwarenessField("user", {
      name: response.data.name,
      color: "#ffcc00",
    });
  });

new Editor({
  element: document.querySelector(".element"),
  extensions: [
    StarterKit.configure({
      history: false,
    }),
    Collaboration.configure({
      document: ydoc,
    }),
    CollaborationCursor.configure({
      provider,
      // user: {name: "John Doe", color: "#ffcc00"},
    }),
  ],
});
